import React, { Component } from "react";
import axios from "axios";
import Textarea from "react-textarea-autosize";

import AdminPostsList from "./AdminPostsList";
import AdminTagsList from "./AdminTagsList";

class AdminMain extends Component {
  state = {
    showBackup: false
  };

  renderBackup() {
    const { showBackup } = this.state;

    return (
      <div>
        <button onClick={() => this.setState({ showBackup: !showBackup })}>
          {showBackup ? "Hide" : "Show"} backup
        </button>
        {showBackup ? (
          <Textarea
            value={JSON.stringify(
              this.props.posts.map(post => {
                let modifiedPost = {
                  ...post,
                  tags: post.tags.map(tag => tag.title)
                };

                delete modifiedPost["mapped_related_posts_tags"];

                return modifiedPost;
              })
            )}
          />
        ) : null}
      </div>
    );
  }

  render() {
    const { headers, onFetchPostsTags, onSetPost, posts, tags } = this.props;

    return (
      <div>
        {this.renderBackup()}

        <div className="admin__lists">
          <AdminPostsList
            headers={headers}
            onFetchPostsTags={onFetchPostsTags}
            onSetPost={onSetPost}
            posts={posts}
            tags={tags}
          />
          <AdminTagsList tags={tags} />
        </div>
      </div>
    );
  }
}

export default AdminMain;
